/* General styles */
body {
  font-family: "Roboto", sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f1f1f1;
  color: #333;
  transition: background-color 0.3s, color 0.3s;
}

.container {
  max-width: 960px;
  margin: 0 auto;
  padding: 1rem;
}

/* Profile section styles */
.profile {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
  margin-top: 1rem;
  text-align: center;
}

.profile img {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  margin-bottom: 1rem;
}

.profile h1 {
  font-size: 2rem;
  margin-bottom: 0.5rem;
}

.profile h2 {
  font-size: 1.5rem;
  font-weight: normal;
}
.profile label{
  font-family: Hack, "Source Code Pro", "Inconsolata", "Ubuntu", "Andale Mono", "Lucida Console", monospace;
  margin-bottom: 1.5rem;
}

/* Project section styles */
#projects {
  margin-top: 2rem;
}

.project {
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-gap: 2rem;
  margin-bottom: 2rem;
}

.project img {
  width: 100%;
}

.project h3 {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}

.project p {
  font-size: 1rem;
  line-height: 1.5;
  margin-bottom: 1rem;
}

  
  .social-media {
  display: flex;
  justify-content: center;
  }
  
  .social-media a {
  display: inline-block;
  width: 40px;
  height: 40px;
  margin: 0 0.5rem;
  border-radius: 50%;
  text-align: center;
  line-height: 40px;
  font-size: 1.5rem;
  transition: background-color 0.3s;
  }
  
  .social-media a:hover {
  background-color: #eee;
  }

  
  /* Light mode */
  .light-mode {
  background-color: #f1f1f1;
  color: #333;
  }
  
  .light-mode .mode-toggle {
  position: fixed;
  bottom: 1rem;
  right: 1rem;
  }
  
  .light-mode .mode-toggle .slider {
  background-color: #333;
  }
  
  /* Dark mode */
  .dark-mode {
  background-color: #333;
  color: #f1f1f1;
  }
  
  .dark-mode .mode-toggle {
  position: fixed;
  bottom: 1rem;
  right: 1rem;
  }
  
  .dark-mode .mode-toggle .slider {
  background-color: #f1f1f1;
  }
  
  /* Mode toggle styles */
  .switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
  }
  
  .switch input {
  opacity: 0;
  width: 0;
  height: 0;
  }
  
  .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: 0.3s;
  }
  
  .slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: 0.3s;
  }
  
  input:checked + .slider {
  background-color: #333;
  }
  
  input:focus + .slider {
  box-shadow: 0 0 1px #333;
  }
  
  input:checked + .slider:before {
  transform: translateX(26px);
  }
  
  .slider.round {
  border-radius: 34px;
  }
  
  .slider.round:before {
  border-radius: 50%;
  }